<template>
	<div class="body">
		<div class="search_group">
			<div class="search_group_top flex_start">
				<div class="search_box">
					<el-select v-model="value8" filterable prefix-icon="el-icon-search" placeholder="母/自管基金名称" clearable>
						<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div class="search_box">
					<el-select v-model="value8" filterable prefix-icon="el-icon-search" placeholder="投资标的类别" clearable>
						<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div class="search_box">
					<el-select v-model="value8" filterable prefix-icon="el-icon-search" placeholder="投资标的全称" clearable>
						<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="search_group_bottom flex_start">
				<div class="search_box">
					<el-select v-model="fileType" @change="chooseFileType" placeholder="文件类型" clearable>
						<el-option v-for="item in options" :key="item.id" :label="item.label" :value="item.id">
						</el-option>
					</el-select>
				</div>
				<div v-if="isBackNumberShow" class="search_box">
					<el-input v-model="value8" placeholder="请输入回款次数" clearable>
					</el-input>
				</div>
				<div v-if="isSecondTypeShow" class="search_box">
					<el-select v-model="value8" placeholder="二级类型" clearable>
						<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div @click="addSecondType" v-if="isSecondTypeShow" class="hover_btn cursor">
					新建二级类型
				</div>
				<div class="btn_box">
					<button @click="">搜索</button>
				</div>
			</div>
		</div>
		<div class="main">
			<div class="table_title flex_between">
				<div class="">
					基金总数<span>(&nbsp;20&nbsp;)</span>
				</div>
				<div class="flex_end">
					<div class="hover_btn flex_center" @click="">
						<span class="iconfont icon-daoru"></span>
						<span>下载文件</span>
					</div>
					<div class="blue_btn flex_center" @click="">
						<span class="iconfont icon-daoru"></span>
						<span>导出报告</span>
					</div>
				</div>
			</div>
			<div class="table">
				<el-table :data="tableData" border style="width: 100%">
					<el-table-column type="selection" width="55">
					</el-table-column>
					<el-table-column sortable prop="name" label="母/自管基金简称" width="200">
					</el-table-column>
					<el-table-column sortable prop="response_partner" label="投资标的类别" width="180">
					</el-table-column>
					<el-table-column sortable prop="manager" label="投资标的全称" width="180">
					</el-table-column>
					<el-table-column sortable prop="subscrib_fund" label="文件类型" width="150">
					</el-table-column>
					<el-table-column sortable prop="subscrib_fund" label="二级文件类型" width="150">
					</el-table-column>
					<el-table-column sortable prop="fund_scale" label="数量" width="180">
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="150">
						<template slot-scope="scope">
							<el-button @click="upload(scope.row)" type="text" size="small">上传文件</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata flex_start">
							<img src="../../images/table_nodata.png">
							<span>没有对应数据</span>
						</div>
					</template>
				</el-table>
				<el-pagination @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="1000">
				</el-pagination>
			</div>
		</div>

		<!-- 弹出框-上传模块开始 -->
		<el-dialog custom-class="pop" append-to-body :title="dialogTitle" :visible.sync="dialogVisible" width="35%" center>
			<div class="dialog_body">
				<el-upload class="upload-demo" drag action="https://jsonplaceholder.typicode.com/posts/" multiple>
					<i class="el-icon-upload"></i>
					<div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
				</el-upload>
			</div>
			<span slot="footer" class="dialog-footer">
			    <el-button @click="dialogVisible = false">取 消</el-button>
			    <el-button type="primary" @click="toAddFile">确 定</el-button>
			</span>
		</el-dialog>
		<!-- 弹出框模块结束 -->
		<!-- 弹出框-添加二级分类模块开始 -->
		<el-dialog custom-class="pop" append-to-body :title="dialogTitleAddSecondType" :visible.sync="dialogVisibleAddSecondType" width="35%" center>
			<div class="dialog_body">
				<div class="pop_input_text">
					新建二级分类
				</div>
				<div class="pop_input_box">
					<el-input v-model="value8" placeholder="请输入回款次数" clearable>
					</el-input>
				</div>
			</div>
			<span slot="footer" class="dialog-footer">
			    <el-button @click="dialogVisibleAddSecondType = false">取 消</el-button>
			    <el-button type="primary" @click="addSecondTypeSubmit">确 定</el-button>
			</span>
		</el-dialog>
		<!-- 弹出框模块结束 -->
	</div>
</template>

<script>
	export default {
		name: 'fundFileInvest',
		data() {
			return {
				currentPage: 1,
				pageSize: 10,
				dialogVisible: false,
				dialogVisibleAddSecondType: false,
				dialogTitle: '添加文件',
				dialogTitleAddSecondType: '',
				fileType: '',
				isBackNumberShow: false,
				isSecondTypeShow: false,
				tableData: [{
					id: 1,
					name: '英雄联盟'
				}],
				options: [
					{
						id: 1,
						value: '选项1',
						label: '出资证明'
					},
					{
						id: 2,
						value: '选项2',
						label: '出资通知'
					},
					{
						id: 3,
						value: '选项3',
						label: '分配说明'
					},
					{
						id: 4,
						value: '选项4',
						label: '投资简报'
					},
					{
						id: 5,
						value: '选项5',
						label: '审计财报'
					},
					{
						id: 6,
						value: '选项6',
						label: '运营报告'
					},
				],
				value8: ''
			}
		},
		created() {

		},
		mounted() {},
		components: {},
		methods: {
			addSecondType(){
				this.dialogVisibleAddSecondType = true;
			},
			addSecondTypeSubmit(){
				this.dialogVisibleAddSecondType = false;
			},
			upload(row) {
				this.dialogVisible = true;
			},
			toAddFile() {
				this.dialogVisible = false;
			},
			chooseFileType(e){
				if (e == 1||e == 2||e == 3) {
					this.isBackNumberShow = true;
					this.isSecondTypeShow = false;
				} else if(e == 4||e == 5||e == 6){
					this.isBackNumberShow = false;
					this.isSecondTypeShow = true;
				} else {
					this.isBackNumberShow = false;
					this.isSecondTypeShow = false;
				}
				var obj = this.getSelectedItem(this.options,e);
				this.dialogTitleAddSecondType = obj.label;
			},
			// 分页功能模块
			changeCurrentPage(e) {
				this.currentPage = e;
			},
			everyPageSizeChange(e) {
				this.pageSize = e;
			},
			// 获取下拉列表的值
			getSelectedItem(data,value){
　　				let obj = {};
　　				obj = data.find((item)=>{
　　　　				return item.id === value;
　　				});
				return obj;
			}, 
		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 30px 20px 30px;
	}
	
	.search_group {
		padding: 10px 0 30px 0;
		border-bottom: 1px solid #F1F1F1;
	}
	
	.search_group_bottom {
		margin-top: 20px;
	}
	
	.search_group_bottom .hover_btn {
		font-size: 14px;
	}
	
	.search_box {
		width: 170px;
		height: 30px;
		margin-right: 13px;
	}
	
	.btn_box {
		margin-left: 24px;
	}
	
	.btn_box button {
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	
	.table_title .flex_center {
		margin-left: 14px;
	}
	.pop_input_text{
		margin: 16px 0;
		font-size: 16px;
		color: #8E8E93;
		text-align: center;
	}
	.pop_input_box{
		width: 270px;
		margin: 0 auto;
	}
</style>


